import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './style.scss';
import x from './img/x.png'


export const Section6mobile = () => {



  const reviews = [
    {
      name: ' ',
      date: '06.03.2021',
      text: ' !   !'
    },
    {
      name: '',
      date: '19.01.2021',
      text: '   . !'
    },
    {
      name: '',
      date: '23.12.2020',
      text: ' ! !'
    },
    {
      name: '',
      date: '23.12.2020',
      text: '     ,    ,    ,         ,           ..'
    },
    {
      name: '',
      date: '23.12.2020',
      text: '     !!!   !!!        ,                   .. !'
    },
    {
      name: '',
      date: '23.12.2020',
      text: ' ! !'
    },
    {
      name: '',
      date: '23.12.2020',
      text: ' ! !'
    }
  ];

  const chunkedReviews = [];
  for (let i = 0; i < reviews.length; i += 1) {
    chunkedReviews.push(reviews.slice(i, i + 1));
  }

  return (
    <div className="reviews-carousel mb-5">
      <h2 className='sect6__otzivi'>  </h2>
      <Carousel>
        {chunkedReviews.map((chunk, index) => (
          <div key={index} className="review-slide">
            {chunk.map((review, index) => (
              <div key={index} className="review">
                <h3>{review.name}</h3>
                <p className="review-date">{review.date}</p>

                <img src={x} alt="" className='sect6__x' />
                <img src={x} alt="" className='sect6__x' />
                <img src={x} alt="" className='sect6__x' />
                <img src={x} alt="" className='sect6__x' />
                <img src={x} alt="" className='sect6__x' />
                <p className='otziv'>{review.text}</p>
              </div>
            ))}
          </div>
        ))}
      </Carousel>
    </div>
  );
}
